*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Gotham";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/gotham-regular.woff2"), url("../fonts/gotham-regular.woff");
  font-display: swap;
}
.page {
  height: 100%;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 360px;
  font-family: "Gotham", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #272424;
  background-color: #ffffff;
}

.container {
  width: 100%;
  max-width: 1300px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.main-header {
  padding: 40px 0 0 0;
  margin-bottom: -2px;
  background-image: url("../img/hero-background-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/hero-background-mobile@2x.png");
  }
}
@media (min-width: 1360px) {
  .main-header {
    padding: 40px 0 0 0;
    margin: 0;
    background-image: url("../img/hero-background-desktop@1x.png");
  }
}
@media (min-width: 1360px) and (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/hero-background-desktop@2x.png");
  }
}

.main-header__title {
  max-width: 330px;
  margin: 65px auto 68px auto;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .main-header__title {
    margin: -90px 0 0 7px;
    max-width: 310px;
    text-align: left;
    justify-self: center;
  }
}
@media (min-width: 1360px) {
  .main-header__title {
    max-width: 585px;
    grid-column: 1/7;
    grid-row: 1/-1;
    font-size: 60px;
    line-height: 70px;
    justify-self: start;
  }
}

.main-header__img {
  display: block;
  margin: 0 auto;
  width: auto;
  height: 345px;
}
@media (min-width: 768px) {
  .main-header__img {
    margin: 0;
  }
}
@media (min-width: 1360px) {
  .main-header__img {
    min-height: 638px;
  }
}

.main-header__logo {
  transition: all 0.3s ease;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}

@media (min-width: 1360px) {
  .main-header__logo-img {
    width: 93px;
    height: 50px;
    margin-left: 6px;
  }
}

.main-header__wrapper {
  margin-left: -15px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-content: center;
    column-gap: 10px;
    padding: 56px 0 0 0;
    margin: 0;
  }
}
@media (min-width: 1360px) {
  .main-header__wrapper {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 5px;
    margin: -5px 0 -3px 0;
  }
}
@media (min-width: 768px) {
  .main-header__wrapper picture {
    justify-self: center;
  }
}
@media (min-width: 1360px) {
  .main-header__wrapper picture {
    grid-column: 1/-1;
    grid-row: 1/-1;
    justify-self: unset;
    margin: 0 -60px 0 390px;
  }
}

