.quality__img {
  position: absolute;
  bottom: 50px;
  left: 50%;
  display: block;
  margin: 0 auto;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .quality__img {
    margin: 0;
    width: auto;
    position: static;
    bottom: unset;
    left: unset;
    transform: unset;
  }
}
@media (min-width: 1360px) {
  .quality__img {
    min-height: 630px;
  }
}

@media (min-width: 768px) {
  .quality__button {
    max-width: 305px;
  }
}

.quality__break {
  display: none;
}
@media (min-width: 1360px) {
  .quality__break {
    display: inline;
  }
}

.button {
  display: block;
  padding: 14px;
  font-family: inherit;
  font-size: 14px;
  line-height: 26px;
  text-align: center;
  border-radius: 50px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
.button--primary {
  color: #e2000f;
  background-color: #ffffff;
  border: 1px solid #e2000f;
}
.button--primary:hover {
  color: #ffffff;
  background-color: #e2000f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.6);
}
.button--secondary {
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
}
.button--secondary:hover {
  color: #e2000f;
  background-color: #ffffff;
  border: 1px solid #e2000f;
}
.button--secondary:active {
  color: rgba(226, 0, 15, 0.3);
}
@media (min-width: 1360px) {
  .button {
    font-size: 18px;
  }
}

